import React from 'react'
import './contact.css'

function Contact() {
    return (
  
             <div id="contact" className="contact">

                 <div className="container">
                     <div className="row justify-content-center">
                         <div className="col-lg-8"> 
                            <div className="contact-title container-titles text-center">
                                <h5>Contact Me</h5>
                            </div>
                         </div>
                     </div>
                 </div>


                 <div className="container " >
                     <div className="row adressArea   text-white">

                         <div className="col-lg-4 ">
                                <div className="adress">
                                    <i className="fa-fw fa fa-envelope-o "></i>
                                    <div className="adresscontent">
                                        <span>MAIL</span>
                                        <p className="mt-2">berkayoral.r@gmail.com</p>
                                    </div>
                                </div>
                         </div>

                         <div className="col-lg-4 ">
                                <div className="adress">
                                    <i className="fa-fw fa fa-phone "></i>
                                    <div className="adresscontent">
                                        <span>PHONE</span>
                                        <p className="mt-2">berkayoral.r@gmail.com</p>
                                    </div>
                                </div>
                         </div>

                         <div className="col-lg-4 ">
                                <div className="adress">
                                    <i className="fa-fw fa fa-linkedin "></i>
                                    <div className="adresscontent">
                                        <span>LINKEDIN</span>
                                        <p className="mt-2">berkayoral.r@gmail.com</p>
                                    </div>
                                </div>
                         </div>


                         <div className="col-lg-12 "  style={{marginTop:"20px"}}>
                              <form action=" " method="post">
                                     <div className="contactform">
                                         <div className="row d-flex ">
                                                <div className="col-lg-6 ">
                                                    <input className="yourName" type="text" name="Your Name" placeholder="Your Name" aria-required="true"/>
                                                </div>
                                                <div className="col-lg-6 ">
                                                    <input className="yourMail" type="text" name="Your Email" placeholder="Your Email" aria-required="true" />
                                                </div>
                                                <div className="col-lg-12">
                                                    <input className="input-area" type="text" name="subject" placeholder="Subject" aria-required="true" />
                                                    <textarea className="text-area" name="textarea"  placeholder="Your Message" aria-required="true"></textarea>
                                                    <input type="submit"  value="Send Message" className="submitBtn"/>
                                                </div>             
                                         </div>
                                     </div>
                              </form>
                         </div>
                       
                     </div>
                 </div>
         
         

             </div>

    )
}

export default Contact
